import Vue from 'vue'
import Router from 'vue-router'

const routes = [
  {
    path: '/',
    meta: {
      title: '报名信息',
      name: 'index'
    },
    component: (resolve) => require(['../views/index'], resolve)
  },
  {
    path: '/success',
    meta: {
      title: '填报成功',
      name: 'success'
    },
    component: (resolve) => require(['../views/success'], resolve)
  }
]
Vue.use(Router)
export default new Router({
  routes: routes
})



